
.share-links h3{margin:6rem 0 1em;font-size:1.28571rem;color:#4b7fe2 !important
}
.share-links .at-resp-share-element .at-share-btn:first-child{margin-left:0;padding-left:0
}
.share-links .at-resp-share-element .at-share-btn svg{fill:#a2a2a2 !important
}
.share-links .at-resp-share-element .at-share-btn:focus,.share-links .at-resp-share-element .at-share-btn:hover{transform:translateY(0)
}
.share-links .at-resp-share-element .at-share-btn:focus svg,.share-links .at-resp-share-element .at-share-btn:hover svg{fill:#4a4a4a !important
}
.share-links .at-resp-share-element .at-share-btn .at-label{font-family:"AvenirNextLTW01","Helvetica Neue",Arial,sans-serif;font-size:.857142rem !important;letter-spacing:.066667em;font-weight:400 !important;text-transform:uppercase
}
.share-links .logo-only .at-share-btn{width:8.333%
}
.share-links .logo-only span.at-icon-wrapper,.share-links .logo-only svg.at-icon{width:28px !important;height:28px !important;line-height:28px !important
}
.share-links .logo-only .at-label{display:none
}
.share-links .text-only span.at-icon-wrapper{display:none
}
.share-links .text-only .at-share-btn:first-child .at-label{margin-left:0
}
.share-links .text-only .at-share-btn:focus .at-label,.share-links .text-only .at-share-btn:hover .at-label{text-decoration:underline
}



