
.node-bworks_footer{padding-top:4rem;margin-bottom:2rem
}
.footer-newsletter{padding-top:4rem;padding-bottom:4rem;background-color:#4b7fe2
}
.footer-newsletter h2{width:100%
}
@media (max-width: 1023px){
.footer-newsletter .mc-field-group{width:83.3333%
}
.footer-newsletter .mc-field-group input{width:100%
}
.footer-newsletter .submit{width:83.3333%;margin-top:8rem
}
}
@media (min-width: 1024px){
.footer-newsletter #mc_embed_signup_scroll{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;padding-left:16.6667%;padding-right:16.6667%
}
.footer-newsletter .mc-field-group{width:50%
}
.footer-newsletter .submit{margin-top:2.7rem;margin-left:10rem
}
}
.footer-newsletter h2,.footer-newsletter label,.footer-newsletter input{color:#fff
}
.footer-newsletter .mc-field-group{position:relative;margin-top:2.2rem
}
.footer-newsletter .mc-field-group label{position:absolute;top:0;transition:transform .2s ease-out;transform:translateY(0.2rem);transform-origin:left top
}
.footer-newsletter .mc-field-group input{position:absolute;top:0;width:100%;font-size:18px;letter-spacing:.066666667em;border:0;border-bottom:1px solid #fff;background-color:transparent;outline:0;transition:background-color .2s ease-out
}
.footer-newsletter .mc-field-group input:focus{background-color:#5888e4
}
.footer-newsletter .mc-field-group input:focus+label,.footer-newsletter .mc-field-group input:not(:focus):valid+label{transform:translateY(-1.2rem) scale(0.75)
}
.footer-newsletter button.animated{padding:0;border:none;color:#fff;background-color:transparent;font-family:"AvenirNextLTW01","Helvetica Neue",Arial,sans-serif;font-weight:700;letter-spacing:.083125em;text-transform:uppercase;cursor:pointer
}
.footer-newsletter button.animated:after{background-color:#fff
}
.footer-newsletter button.animated:focus,.footer-newsletter button.animated:hover{color:#4b7fe2
}
.footer-newsletter .submit{text-align:center
}
.footer-contact{padding-top:4rem;padding-bottom:4rem
}
.footer-address{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;padding-top:2rem;padding-bottom:1.5rem;background-color:#f3f3f3
}
.footer-address a{color:#a2a2a2;font-weight:normal
}
.footer-hosted{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.footer-hosted .icon{margin-right:1.75rem
}
.footer-addresses{color:#a2a2a2
}
.footer-social-links{color:#a2a2a2
}
.footer-social-links>a{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.footer-social-links .icon{margin-right:1rem
}
@media (max-width: 1979px){
.footer-social-links .text{display:none
}
}
@media (max-width: 767px){
.footer-hosted{-ms-flex:1 100%;flex:1 100%;margin-bottom:2rem
}
.footer-addresses{-ms-flex:2 83.3333%;flex:2 83.3333%
}
.footer-social-links{-ms-flex:1 auto;flex:1 auto
}
.footer-social-links .icon{display:block;margin-bottom:.66rem
}
}
@media (min-width: 768px) and (max-width: 1023px){
.footer-hosted{-ms-flex:1 27.27272%;flex:1 27.27272%;display:block
}
.footer-hosted .text{display:block;margin-top:.5rem;padding-right:8.33333%
}
.footer-addresses{-ms-flex:1 72.727273%;flex:1 72.727273%;display:-ms-flexbox;display:flex
}
.footer-addresses>div{-ms-flex:1 50%;flex:1 50%
}
.footer-social-links{-ms-flex:1 100%;flex:1 100%;margin-top:2rem;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around
}
}
@media (min-width: 1024px){
.footer-address{-ms-flex-align:end;align-items:flex-end
}
.footer-hosted{-ms-flex:1 100%;flex:1 100%
}
.footer-hosted .text{max-width:41.6667%
}
.footer-addresses{-ms-flex-order:3;order:3;-ms-flex:1 50%;flex:1 50%;display:-ms-flexbox;display:flex;-ms-flex-align:end;align-items:flex-end
}
.footer-addresses>div{-ms-flex:1 50%;flex:1 50%
}
.footer-addresses p:last-child{margin:0
}
.footer-social-links{-ms-flex-order:2;order:2;-ms-flex:1 50%;flex:1 50%;margin-top:2rem;display:-ms-flexbox;display:flex;-ms-flex-pack:start;justify-content:flex-start
}
.footer-social-links>a{min-width:16.66667%
}
}
@media (min-width: 1024px){
.footer-social-links>a{margin-right:2rem
}
}



