
.node-bworks_project_block .view-all{text-align:center
}
body .fp-tableCell{vertical-align:bottom
}
body #fp-nav{position:fixed;right:4.1617%;top:50%
}
body #fp-nav ul li{width:17px;height:9px;margin:0 0 4rem
}
body #fp-nav ul li a{height:auto;width:auto
}
body #fp-nav ul li a span{left:auto;top:auto;width:9px;height:9px;margin:0;transform:translateX(-50%);background-color:#fff;border-radius:0
}
body #fp-nav ul li a.active span{width:17px;height:17px;margin:0;border-radius:0
}
body #fp-nav ul li:not(:last-of-type):after{display:block;content:"";position:absolute;width:1px;height:4rem;transform:translateY(9px);background-color:#fff
}



