
.node-bworks_article img{max-width:100%;height:auto
}
.node-bworks_article .date{font-weight:400;color:#a2a2a2
}
.node-bworks_article.teaser{padding-top:4rem
}
.node-bworks_article.teaser .article{padding-top:4rem;padding-bottom:5rem;background-color:#f3f3f3
}
@media (max-width: 767px){
.node-bworks_article.teaser .article{padding-top:4rem
}
.node-bworks_article.teaser .image{width:calc(100% + 16.6677vw);margin-left:calc(55% - 50vw);margin-right:calc(55% - 50vw);margin-top:-4rem;padding:0
}
.node-bworks_article.teaser h2{margin-top:3rem
}
}
.node-bworks_article.teaser .h1{color:#4a4a4a
}
.node-bworks_article.teaser .read-more{margin-top:2rem;text-align:right
}
.node-bworks_article.teaser .read-more a{color:#a2a2a2
}
.node-bworks_article.teaser .read-more a:focus,.node-bworks_article.teaser .read-more a:hover{color:#4a4a4a
}
.node-bworks_article.teaser .view-all{margin-top:4rem;text-align:center
}
.node-bworks_article.teaser .view-all a{color:#a2a2a2
}
.node-bworks_article.teaser .view-all a:focus,.node-bworks_article.teaser .view-all a:hover{color:#4a4a4a
}
.node-bworks_article.teaser .view-all a:focus,.node-bworks_article.teaser .view-all a:hover{color:#fff
}
.node-bworks_article.teaser .view-all a:after{background-color:#a2a2a2
}
.node-bworks_article.list img{object-fit:cover;object-position:center;max-height:12rem;min-height:12rem
}
.node-bworks_article.list .category{color:#a2a2a2;margin-top:1em;padding-top:.5em;border-top:1px solid #a2a2a2
}
.node-bworks_article.list h2{margin:.25em 0 .5em;text-transform:none;transition:color .4s
}
.node-bworks_article.list:not(:hover) h2{color:#4a4a4a
}



