
.node-bworks_team_member .photo-wrapper{position:relative;overflow:hidden
}
.node-bworks_team_member img{position:relative;max-width:100%;height:auto;filter:grayscale(100%);background-color:#4b7fe2;background-blend-mode:multiply;z-index:1
}
@supports (mix-blend-mode: multiply){
.node-bworks_team_member .overlay{position:absolute;left:0;top:0;right:0;bottom:0;background-color:#4b7fe2;mix-blend-mode:multiply;z-index:2;transition:transform .4s
}
}
.node-bworks_team_member .info-wrapper{padding-top:.5rem;padding-bottom:2rem;transform:translateY(-101%);transition:transform .4s
}
.node-bworks_team_member .lastname,.node-bworks_team_member .surname{margin:0;text-transform:none
}
.node-bworks_team_member .position{margin-top:.5rem;font-size:1.14285rem;font-weight:400;color:#4a4a4a
}
@media (max-width: 767px){
.node-bworks_team_member .lastname,.node-bworks_team_member .surname{font-size:1rem;line-height:1.16667;letter-spacing:.1225em
}
.node-bworks_team_member .position{font-size:.85714rem;line-height:1.36363;letter-spacing:.07272em
}
}
.node-bworks_team_member:focus .overlay,.node-bworks_team_member:hover .overlay{transform:translateX(101%)
}
.node-bworks_team_member:focus .info-wrapper,.node-bworks_team_member:hover .info-wrapper{transform:translateY(0)
}



