
.job-wrapper{padding-left:8.3333%;padding-right:8.3333%
}
ul.job-links{padding:0;list-style:none
}
ul.job-links a{display:inline-block;width:85%;margin-bottom:1rem;padding-top:.5em;border-top:1px solid #4a4a4a;font-weight:400;color:#4a4a4a
}
ul.job-links a:after{content:"•";position:absolute;right:calc(15% + 1rem);color:transparent;transition:transform .4s
}
ul.job-links a.active:after{color:#4b7fe2;transform:scale(2)
}
ul.job-links a:focus,ul.job-links a:hover{color:#4b7fe2
}
ul.job-links a:focus:after,ul.job-links a:hover:after{color:#4b7fe2;transform:scale(2)
}
.job-offer{margin-top:6rem
}
.job-offer .subtitle{margin:-1rem 0 2rem;color:#4a4a4a
}
.job-offer .apply{margin-top:4rem;text-align:center
}
.job-description h3{margin:3.5em 0 1.16667em;font-size:.857142857rem;line-height:1.166667;letter-spacing:.044167em;color:#4a4a4a
}
.job-description ul{margin:0;padding:0;list-style:none
}
.job-description ul li{padding-left:1.5em
}
.job-description ul li:before{content:"–";margin-left:-1.5em;padding-right:1em
}



